body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Content {
  margin-top: 100px;
}

.header {
  text-align: center;
  width: 100%;
  height: 40px;
  background-color: #24777b6e;
  padding: 10px 0;
  position: absolute;
  top: 0;
}

.header h1 {
  margin: 0;
  padding: 0;
  color: #00002b;
}

.search {
  margin-top: 100px;
  text-align: center;
}

input {
  background-color: #24777b26;
  border: 1px solid #00002b;
  font-weight: 300;
  font-size: 0.875em;
  padding: 10px;
  position: relative;
  -webkit-transition: 0.3s all ease-out;
  transition: 0.3s all ease-out;
  width: 20em;
  color: #000;
}

input:focus,
input:active {
  outline: none;
}

.Article {
  width: 250px;
  padding: 16px;
  text-align: left;
  border: 1px solid #eee;
  box-shadow: 0 2px 3px #ccc;
  margin: 10px;
  box-sizing: border-box;
  color: #00002b;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.Author {
  margin: 16px 0;
  font-weight: lighter;
  font-size: 14px;
}

.Article:hover,
.Article:active {
  background-color: #fed411;
  -webkit-transition: 0.3s all ease-out;
  transition: 0.3s all ease-out;
}

.title {
  font-weight: 500;
}

.date {
  font-size: 0.8rem;
  font-weight: 200;
}

a,
.link,
.save {
  text-decoration: none;
  cursor: pointer;
}

.link {
  font-weight: 100;
  color: #24777b;
}

.link:hover,
.link:active {
  font-weight: 400;
}

.save {
  position: relative;
  float: right;
  top: -15px;
  width: 45px;
  text-align: center;
  color: #fff;
  background-color: #24777b;
}

.Results {
  width: 85%;
  margin: 50px auto;
  min-height: 200px;
  max-height: 100%;
}

/* For the pull out drawer
.results-container {
  margin-bottom: 80px;
} */

.sectionArticles {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  margin: auto;
}

.section h3 {
  margin-left: 10px;
}

.SavedSection {
  width: 85%;
  margin: 50px auto 0px auto;
  background-color: #00002b;
  color: #ffffff;
  width: 100%;
  height: 600px;
  /* position: fixed; */
}

/* .drawer-closed {
  bottom: -545px;
} */

.SavedSection h2 {
  text-align: center;
  padding: 10px 0 15px 0;
  margin: auto;
}

.saved-articles {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  width: 85%;
  margin: auto;
  overflow: scroll;
  height: 85%;
}

.SavedArticle {
  width: 250px;
  padding: 16px;
  text-align: left;
  border: 1px solid #a0c4c7;
  box-shadow: 0 2px 3px #dfebeb;
  margin: 10px;
  box-sizing: border-box;
  color: #ffffff;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  background-color: #499094;
  height: 221px;
}

.Author {
  margin: 16px 0;
  font-weight: lighter;
  font-size: 14px;
}

.SavedArticle:hover,
.SavedArticle:active {
  background-color: #24777b;
  -webkit-transition: 0.3s all ease-out;
  transition: 0.3s all ease-out;
}

.title {
  font-weight: 500;
}

.date {
  font-size: 0.8rem;
  font-weight: 200;
}

a,
.savedLink,
.delete {
  text-decoration: none;
  cursor: pointer;
}

.savedLink {
  font-weight: 100;
  color: #ffffff;
}

.savedLink:hover,
.savedLink:active {
  font-weight: 400;
}

.delete {
  position: relative;
  float: right;
  top: -15px;
  width: 60px;
  text-align: center;
  color: #00002b;
  background-color: #fed411;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

h1,
h2 {
  font-family: "Abril Fatface", serif;
}

h1 {
  font-weight: 600;
}

h2 {
  font-weight: 500;
}

